@import "https://fonts.googleapis.com/css2?family=Comfortaa:wght@500&display=swap";
body {
  font-family: Comfortaa, cursive;
}

.game-over-popup {
  color: #fff;
  text-align: center;
  background-color: #4d4a4acc;
  border-radius: 10px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.buttons {
  margin-top: 20px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 20px;
}

button:hover {
  background-color: #0056b3;
}

.highscore {
  background-color: #f5f5f5;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  width: 600px;
  height: 50vh;
  margin-left: 2rem;
  padding: 20px;
  display: flex;
}

.highscore h1 {
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
}

.player-score {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
}

.player-score h3 {
  color: #333;
  font-size: 24px;
}

.player-score .score {
  color: #007bff;
  font-size: 20px;
  font-weight: bold;
}

